import React, { Component } from 'react';

class LcboApi extends Component{
    
    constructor(){
        super();
        this.state = {
               products: [],
                 };
    }
    
    componentWillMount(){
        //called the first time the component is loaded right before the component is added to the pageX
        //esentially init
                //called after the component has been renderd into the page
              fetch({url:"https://lcboapi.com/Products/",
                        headers:{'Authorization':
                        'MDphNjhjNGUxZS01Yjc1LTExZTgtYjkzMy01M2EwZDBjMGQ1NDM6ako4YTU4RDRvU2tVanBHR0NjYmM5VGFUZzI3YVJ2QXBLbkw5'
      },
      method: 'GET',
      mode: 'cors',}).then(response => response.json())
      .then(parsedJSON=> console.log(parsedJSON.results))
      .catch(error => console.log('parsing failed'))
    }
    
    componentDidMount(){

    
    }

    componentWillReceiveProps(nextProps) {
        //called when the props provided to the components are changed
    }
  
  componentWillUpdate(nextProps, nextState) {
      //called when the props and or state changes
    }

    componentWillUnmount(){
        //called when the component is removed
    }
    
    render() {
        return(
    <p> testing </p>
        )
        
    }
    
    
}
export default LcboApi;



